<template>
  <div class="submit">
    <van-nav-bar title="提交订单" left-text="返回" @click-left="goShopcart" fixed />
    <div class="content">
      <div class="optionAddress" @click="isShow=true">{{address}}</div>
      <div class="item clearFix">
        <div class="content-list" v-for="(item , index) in shopcartData" :key="index">
          <div class="imgBox fl">
            <img class="auto-img" :src="item.small_img" alt />
          </div>
          <div class="content-text fl">
            <div class="up clearFix">
              <span class="name fl">{{item.name}}</span>
              <span class="price fr">￥{{item.price}}</span>
            </div>
            <div class="center">
              <span class="enname">{{item.enname}}</span>
            </div>
            <div class="down clearFix">
              <span class="rule fl">{{item.rule}}</span>
              <span class="count fr">x {{item.count}}</span>
            </div>
          </div>
        </div>
        <div class="total-box fr">
          <span class="total-text">共计 {{count}} 件 商品 合计:</span>
          <span class="total-price">￥{{total}}</span>
        </div>
      </div>
    </div>
    <van-submit-bar
      :disabled="false"
      :price="total * 100"
      button-text="立即结算"
      tip-icon="info-o"
      @submit="submit"
    />
    <van-popup v-model="isShow" round position="bottom" class="popup">
      <div class="title">收货地址</div>
      <van-address-list
      v-model="chosenAddressId"
      :list="addressMsg"
      default-tag-text="默认"
      @add="goAddress"
      @edit="goAddress"
      class="list"
      @select="selectAddress"
    />
    </van-popup>
  </div>
</template>

<script>
import "../assets/css/submit.less";
export default {
  data() {
    return {
      isShow: false,
      sids: [],
      address: "请选择收货地址",
      shopcartData: [],
      count: 0,
      total: 0,
      chosenAddressId: "1",
      addressMsg: [
      ],
    };
  },
  created() {
    this.sids = this.$route.query.sids.split("-");
    // 
    this.getShopcart(this.sids);
    this.getAddress();
    if(localStorage.getItem("address")){
      
      
      this.address = localStorage.getItem("address");
      localStorage.setItem("address","")
    }
  },
  methods: {
    // 返回上一页
    goShopcart() {
      this.$router.push({name:"Shopcart"});
    },
    // 跳转到 新增/编辑 地址页面
    // goaddress() {
    //   this.$router.push({ name: "Address" });
    // },
    goAddress(address) {
      let obj = {
          name:"Address"
      };
      if(address){
          obj.params = {
              aid:address.id
          }
      }
      this.$router.push(obj);
    },
    // 获取结算商品数据
    getShopcart(sids) {
      let tkStr = localStorage.getItem("__Tk");
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        // 延迟关闭加载框时间 为0则不自动关闭
        duration: 0
      });
      this.axios({
        method: "get",
        url: "/commitShopcart",
        params: {
          appkey: this.appkey,
          tokenString: tkStr,
          sids: JSON.stringify(sids)
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == 50000) {
            result.data.result.forEach(val => {
              this.count += val.count;
              this.total += val.count * val.price;
            });
            this.shopcartData = result.data.result;
            // 
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },
    // 获取地址
    getAddress() {
      let tkStr = localStorage.getItem("__Tk");
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        // 延迟关闭加载框时间 为0则不自动关闭
        duration: 0
      });
      this.axios({
        method: "get",
        url: "/findAddress",
        params: {
          appkey: this.appkey,
          tokenString: tkStr
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == 20000) {
            result.data.result.forEach(val => {
              let data = {
                id: val.aid,
                name: val.name,
                tel: val.tel,
                address:
                  val.province + val.city + val.county + val.addressDetail,
                isDefault: val.isDefault
              };
              if (val.isDefault) {
                this.address = data.name + " " + data.tel + " " + data.address;
              }
              this.addressMsg.push(data);
            });
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },
    // 提交订单
    submit(sids) {
      if (this.address == "请选择收货地址") {
        this.$toast("请选择收货地址");
        return;
      }
      let tkStr = localStorage.getItem("__Tk");
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
      this.axios({
        method: "post",
        url:"/pay",
        data: {
          appkey: this.appkey,
          tokenString: tkStr,
          sids: JSON.stringify(this.sids),
          phone: this.shopcartData.tel,
          address: this.shopcartData.address,
          receiver: this.shopcartData.receiver
        }
      })
        .then(result => {
          this.$toast.clear();
          
          this.$router.push({name:"Order"});
        })
        .catch(err => {
          
        });
    },
    // 选择地址
    selectAddress(item){
      // 
      this.isShow = false,
      this.address = item.name +" " + item.tel + " " + item.address 
    },
    // 删除地址
    removeAddress() {
      let tkStr = localStorage.getItem("__Tk");
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
      this.axios({
        method: "post",
        url: "/deleteAddress",
        data: {
          appkey: this.appkey,
          tokenString: tkStr,
          aid: this.aid
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if(result.data.code == 10000){
            this.$toast.success(result.data.msg);
            this.$router.push({name:"Address"});
          }else{
            this.$toast(result.data.meg)
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },
  }
};
</script>

